import { Form, Formik } from 'formik';
import Footer from 'components/Footer';
import Header from 'components/Header';
import React from 'react';
import { useStyles } from './track.style';
import trackBack from 'assets/icons/trackBack.png'
import { Paper, Typography } from '@material-ui/core';
import { BonfireField } from 'components/BonfireField/BonfireField';
// url hissesine token qoy hemin tokene uygun track - i tap
export const Track = () => {
    const c = useStyles();
    return (
        <>
            <Header />
            <div className={c.container}>
                <div className={c.modal}>
                    <img src={trackBack} />
                    <Paper className={c.boxes}>
                        <Typography className={c.subTitle}>Track your order</Typography>
                        <Typography className={c.txt}>Please enter your order number below. You can findit in the confirmation email we or by following</Typography>
                        <span className={c.blueLink}>these steps.</span>
                        <Typography className={c.txt} style={{ alignSelf: "flex-start" }}>ORDER #</Typography>
                        <Formik
                            enableReinitialize
                            initialValues={{}}
                            onSubmit={() => { }}
                        >
                            {({ }): React.ReactNode => {
                                return (
                                    <Form>
                                        <BonfireField
                                            name="track"
                                            placeholder=""
                                        />
                                        <button className="btn-primary btn-sm"
                                            onClick={() => { }}
                                            style={{ width: "100%", height: 50, marginTop: 20 }}
                                        >
                                            TRACK MY ORDER
                                        </button>
                                    </Form>
                                )
                            }
                            }
                        </Formik>
                    </Paper>
                </div>
            </div>
            <Footer />
        </>
    )
}